import { fetchEndpoint } from '../server/network';

// 
export const authSocial =
(vendor, access_token, open_id, invite_code, js_code) =>
fetchEndpoint(
  '/v2/ecapi.auth.social',
  'POST',
  {
    "vendor": vendor,  // 
    "access_token": access_token,  // OAUTH TOKEN
    "open_id": open_id,  // ID
    "invite_code": invite_code,  // ID
    "js_code": js_code,  // 
  }
)




