// initial state
const initState = {
	isShowProductModel: false,  // 
	isSearch: false //
};

const state = {
	...initState,
	initState(){
        return initState
    }
}

// mutations
const mutations = {
	// 
	changeIsShowProductModel(state, value) {
		state.isShowProductModel = value;
	},
	changeSearch(state, value) {
		state.isSearch = value;
	}
}

export default {
	state,
	mutations,
}



