<script>
export default {
  methods: {
    getPromoTitle(item) {
      let title = null
      let promo = item.promo
      switch (promo) {
        case 'preferential':
        {
          title = ''
        }
        break;
        case 'cashgift':
        {
          title = ''
        }
        break;
        case 'score':
        {
          title = ''
        }
        break;
        case 'order_reduction':
        {
          title = ''
        }
        break;
        case 'goods_reduction':
        {
          title = ''
        }
        break;
        case 'coupon_reduction':
        {
          title = ''
        }
        break;
        case 'combined_promos':
        {
          title = ''
        }
        break;
        case 'transportTitle':
        {
          title = ''
        }
        break;          
      
        default:
          break;
      }
      return title
    },
  },
}
</script>



