// 
export const productPurchase =
(shop, product, property, amount, consignee, shipping, invoice_type, invoice_title, invoice_content, coupon, cashgift, comment, score, exchange_score) =>
fetchEndpoint(
  '/v2/ecapi.product.purchase',
  'POST',
  {
    "shop": shop,  // ID
    "product": product,  // ID
    "property": property,  // ID
    "amount": amount,  // 
    "consignee": consignee,  // ID
    "shipping": shipping,  // ID
    "invoice_type": invoice_type,  // ID
    "invoice_title": invoice_title,  // 
    "invoice_content": invoice_content,  // ID
    "coupon": coupon,  // ID
    "cashgift": cashgift,  // ID
    "comment": comment,  // 
    "score": score,  // 
    "exchange_score": exchange_score  // ( )    
  }
)




