import { fetchEndpoint } from '../server/network';

// 
export const paymentPay =
(order, code, referer, channel, openid) =>
fetchEndpoint(
  '/v2/ecapi.payment.pay',
  'POST',
  {
    "order": order,  // ID
    "code": code,  // code  ehking  pay.alipay     pay.weixin  pay.unionpay    pay.code  pay.teegon  
    "referer": referer,  // code  ehking  pay.alipay     pay.weixin  pay.unionpay    pay.code  pay.teegon  
    "channel": channel,  // code  ehking  pay.alipay     pay.weixin  pay.unionpay    pay.code  pay.teegon  
    "openid": openid,
  }
)

// 
export const paymentTypesList =
() =>
fetchEndpoint(
  '/v2/ecapi.payment.types.list',
  'POST',
  {
  }
)




