import { fetchEndpoint } from '../server/network';

// 
export const withdraw =
(cash, memo) =>
fetchEndpoint(
  '/v2/ecapi.withdraw.submit',
  'POST',
  {
    "cash": cash,  // 
    "memo": memo,  // 
  }
)

// 
export const withdrawCancel =
(id) =>
fetchEndpoint(
  '/v2/ecapi.withdraw.cancel',
  'POST',
  {
    "id": id,  // 
  }
)

// 
export const withdrawList =
(page, per_page) =>
fetchEndpoint(
  '/v2/ecapi.withdraw.list',
  'POST',
  {
    "page": page,  // 
    "per_page": per_page,  // 
  }
)


