import { fetchEndpoint } from '../server/network';

// 
export const authMobileSend =
(code, mobile) =>
fetchEndpoint(
  '/v2/ecapi.auth.mobile.send',
  'POST',
  {
    "code": code,  //  
    "mobile": mobile,  // 
  }
)

// 
export const authMobileVerify =
(code, mobile) =>
fetchEndpoint(
  '/v2/ecapi.auth.mobile.verify',
  'POST',
  {
    "code": code,  //  
    "mobile": mobile,  // 
  }
)

// 
export const authMobileSignup =
(mobile, code, password, invite_code) =>
fetchEndpoint(
  '/v2/ecapi.auth.mobile.signup',
  'POST',
  {
    "mobile": mobile,  // 
    "code": code,  // 
    "password": password,  // 
    "invite_code": invite_code,  // id
  }
)

// 
export const authMobileReset =
(mobile, code, password) =>
fetchEndpoint(
  '/v2/ecapi.auth.mobile.reset',
  'POST',
  {
    "mobile": mobile,  // 
    "code": code,  // 
    "password": password,  // 
  }
)

// 
export const authMobileBinding =
(mobile, code, password) =>
fetchEndpoint(
  '/v2/ecapi.auth.mobile.binding',
  'POST',
  {
    "mobile": mobile,  // 
    "code": code,  // 
    "password": password,  // 
  }
)




