import { fetchEndpoint } from '../server/network';

// 
export const authDefaultSignup =
(username, email, password, invite_code) =>
fetchEndpoint(
  '/v2/ecapi.auth.default.signup',
  'POST',
  {
    "username": username,  // 
    "email": email,  // 
    "password": password,  // 
    "invite_code": invite_code,  // id
  }
)

// 
export const authDefaultReset =
(email) =>
fetchEndpoint(
  '/v2/ecapi.auth.default.reset',
  'POST',
  {
    "email": email,  // 
  }
)




