<template>
	<div class="cart-header-wrapper ui-commmon-header">
		<img src="../../../assets/image/change-icon/back@2x.png" v-if="!issShowTabbar" @click="goBack()" class="ui-go-back">
		<h3></h3>
		<span @click="changeFinishStatus()" v-if="!isFinish && !isEmpty"></span>
		<span @click="changeFinishStatus()" v-if="isFinish"></span>
	</div>
</template>

<script>
	import { mapState, mapMutations } from 'vuex';
	export default {
		data(){
			return {
				isFinish: false // -false    -  true  s - true - false
			}
		},

		props: {
			issShowTabbar: {
				type: Number,
				default: 0
			},
			isEmpty: {
				type: Boolean,
				default: false
			}
		},

		computed:{},

		methods: {

			/*
			 * goBack: 
			 */
			goBack() {
				this.$router.go(-1);
			},

			/*
			 *  changeFinishStatus:    , 
			 */
			changeFinishStatus() {
				this.isFinish = !this.isFinish;
				let data = {'isFinish': this.isFinish};
				this.$parent.$emit('change-list-selected', data);
			}
		}
	}
</script>

<style lang='scss' scoped>
	.cart-header-wrapper {
		position: fixed;
		width: -webkit-fill-available;
		span {
			position: absolute;
			font-size:15px;
			color:rgba(78,84,93,1);
			display: inline-block;
			height: 44px;
			line-height: 44px;
			top: 0px;
			right: 15px;
		}
	}
</style>



