import wx from 'weixin-js-sdk'
import { configGet } from '../api/config'

const wxApi = {
	/*
	 * [isweixin ]
	 */
	isweixin() {
		const ua = window.navigator.userAgent.toLowerCase()
		if (ua.match(/MicroMessenger/i) == 'micromessenger') {
			return true
		} else {
			return false
		}
	},

	/*
	 * getConfigRes config
	 */
	getConfigRes(title, imgUrl, desc) {
		// TODO:
		let that = this;
		let url = window.location.href.split('#')[0] + '?#' + window.location.href.split('#')[1];
		// jssdkConfig(window.location.href.split('#')[0]).then(res => {
		// 	if (res) {
		// 		let wechatConfig = res.config;
		// 		that.wxRegister(wechatConfig, title, imgUrl, desc);
		// 	}
		// })
	},

	/*
	 * [wxRegister Api]
	 * @param  {Function} callback [ready]
	 */
	wxRegister(config, title, imgUrl, desc, url, callback) {		
		wx.config({
			debug: false, // 
			appId: config.app_id, // 
			timestamp: config.timestamp, // 
			nonceStr: config.nonceStr, // 
			signature: config.signature, // 1
			jsApiList: [
				'onMenuShareTimeline', // 
				'onMenuShareAppMessage', // 
				'onMenuShareQQ', // QQ
				'onMenuShareWeibo' // 
			] // JSJS2
		})

		wx.ready(function () {						
			imgUrl = imgUrl ? imgUrl : require('../assets/image/change-icon/app-icon.png');
			let link = url
			desc = desc ? desc : '';
			wx.onMenuShareAppMessage({
				title: title, // 
				imgUrl: imgUrl, // 
				desc: desc, // 
				link: link,
				success: function() {
				},
				error: function () {
				}
			})

			wx.onMenuShareTimeline({
				title: title, // 
				link: link, // 
				imgUrl: imgUrl, // 
				success() {
					// 
				},
				cancel() {
					// 
				}
			})

			wx.onMenuShareQQ({
				title: title, // 
				link: link, // 
				imgUrl: imgUrl, // 
				desc: desc,
				success() {
					// 
				},
				cancel() {
					// 
				}
			})

			wx.onMenuShareWeibo({
				title: title, // 
				link: link, // 
				imgUrl: imgUrl, // 
				success() {
					// 
				},
				cancel() {
					// 
				}
			})
		});

		wx.error(res => {
			// alert(':' + res.errMsg);
			// console.log(res);
		});
	},
}

export default wxApi



